<template>
    <div>
        <adt-search-folder
            :searchRouter="{query: {taskNumber: search.taskNumber, taskName: search.taskName, taskClassify: search.taskClassify}}"
            @on-triggerFolder="searchChange" :isStatus="true">
            <template slot="status">
                <div class="status">
                    <Col span="10">
                        <div class="name">审核状态:</div>
                        <span v-for="(status, id) in auditStatusList" :key="id" @click="auditStatus(id)" :class="{'click-status': auditStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                    <Col span="14">
                        <div class="name">启用状态:</div>
                        <span v-for="(status, id) in enableStatusList" :key="id" @click="enableStatus(id)" :class="{'click-status': enableStatusNum == id}">
                            {{status.status}}
                        </span>
                    </Col>
                </div>
            </template>
            <template slot="always-show">
                <Col span="6" class="search-condition">
                    <span>附加任务编号:</span>
                    <Input v-model="search.taskNumber" clearable></Input>
                </Col>
                <Col span="6" class="search-condition line-margin">
                    <span>附加任务名称:</span>
                    <Input v-model="search.taskName" clearable></Input>
                </Col>
                <Col span="6" class="search-condition line-margin">
                    <span>附加任务分类:</span>
                    <Select v-model="search.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </Col>
            </template>
        </adt-search-folder>
        <div class="btns-paginator">
            <AdtButton>
                <Button @click="add()">
                    新增附加任务<Icon type="ios-add-circle-outline"></Icon>
                </Button>
            </AdtButton>
            <adt-paginator @getPages="getPages" class="pagination" :currentPage="~~page" :total="~~total" />
        </div>
        <Table :columns="tableColumns" :data="tableData" stripe>
            <!-- 审核状态 -->
            <template slot-scope="{ row, index }" slot="auditStatus">
                <span :class="{success: row.statusCode == 'pass', failure: row.statusCode == 'failed'}">{{row.status}}</span>
            </template>
            <!-- 启用状态 -->
            <template slot-scope="{ row, index }" slot="enableStatus">
            </template>
            <!-- 操作 -->
            <template slot-scope="{ row, index }" slot="action">
                <Icon class="icon-action" type="md-create" @click="edit(row)" />
                <Icon class="icon-action" type="md-close" @click="remove(row)" />
                <Icon class="icon-action" type="ios-alert-outline" @click="detail(row)" />
            </template>
        </Table>
        <adt-my-modal
            title="新增附加任务"
            :showModal="isModalShow"
            @on-visible-change="addvisibleChange"
            @on-ok="save()"
            @on-cancel="cancel()"
            width="650">
            <Form
                ref="formValidate"
                inline
                :model="taskData"
                :rules="ruleValidate"
                :label-colon="true"
                label-position="left"
                :label-width="120"
                class="two-column">
                <FormItem label="附加任务名称" prop="taskName">
                    <Input v-model="taskData.taskName" />
                </FormItem>
                <FormItem label="附加任务分类" prop="taskClassify">
                    <Select v-model="taskData.taskClassify" clearable>
                        <Option v-for="(item, index) in taskClassifyList" :key="index" :value="item.value">{{ item.label }}</Option>
                    </Select>
                </FormItem>
            </Form>
            <h6>选择数据项</h6>
            <Table :columns="formTableColumns" :data="formTableData" @on-selection-change="selectItem" stripe>
            </Table>
        </adt-my-modal>
        <adt-reconfirm-modal :showModal="isActionDel" @on-visible-change="visibleChange" @on-ok="allDelClick()" />
    </div>
</template>

<script>
import paginator from '@/mixins/paginator'
import validateRules from '@/libs/validateRules'
export default {
    mixins: [paginator],
    data () {
        return {
            isModalShow: false,
            isActionDel: false,
            search: {},
            taskClassifyList: [
                {
                    label: '运输',
                    value: 'ys'
                },
                {
                    label: '场所',
                    value: 'ys'
                }
            ],
            auditStatusNum: '',
            enableStatusNum: '',
            auditStatusList: [
                {code: 'unreviewed', status: '未审核'},
                {code: 'stock', status: '未货好'},
                {code: 'progress', status: '执行中'}
            ],
            enableStatusList: [
                {code: 'unreviewed', status: '已启用'},
                {code: 'stock', status: '未启用'},
                {code: 'progress', status: '已禁用'}
            ],
            taskData: {
                taskName: '',
                taskClassify: ''
            },
            ruleValidate: {
                taskName: [
                    validateRules.required('附加任务名称')[0]
                ],
                taskClassify: [
                    validateRules.select('附加任务分类')[0]
                ]
            },
            selectedItem: [],
            formTableColumns: [
                {
                    title: '选择',
                    align: 'center',
                    type: 'selection',
                    width: 60
                },
                {
                    title: '数据项编码',
                    align: 'center',
                    key: 'accountName'
                },
                {
                    title: '数据项展示名称',
                    align: 'center',
                    key: 'organizationId'
                }
            ],
            formTableData: [],
            tableColumns: [
                {
                    title: '附加任务编号',
                    align: 'center',
                    key: 'accountName',
                    tooltip: true
                },
                {
                    title: '附加任务名称',
                    align: 'center',
                    key: 'organizationId'
                },
                {
                    title: '物流类型',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '运输类型',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '创建人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '创建时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '修改人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '修改时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '审核人',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '审核状态',
                    align: 'center',
                    slot: 'auditStatus'
                },
                {
                    title: '审核时间',
                    align: 'center',
                    key: 'phoneNumber'
                },
                {
                    title: '启用状态',
                    align: 'center',
                    slot: 'enableStatus'
                },
                {
                    title: '备注',
                    align: 'center',
                    slot: 'enableStatus'
                },
                {
                    title: '操作',
                    align: 'center',
                    key: 'operating',
                    slot: 'action',
                    width: 100
                }
            ],
            tableData: [
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核通过', statusCode: 'pass'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'failed'},
                {accountName: '11', organizationId: '22', phoneNumber: '33', accountEmail: '44', status:'审核失败', statusCode: 'unreviewed'}
            ]
        }
    },
    methods: {
        getPages (page) {
            this.page = page
        },
        addvisibleChange (changeStatus) {
            this.isModalShow = changeStatus
        },
        visibleChange (changeStatus) {
            this.isActionDel = changeStatus
        },
        dataInit () {
            this.getData()
        },
        getData () {},
        add () {
            this.isModalShow = true
            this.selectedItem = []
        },
        searchChange () {},
        // 编辑
        edit (id) {
            this.isModalShow = true
            this.selectedItem = []
            apiGetTask({ data: { id } }).then(({ data }) => {
                for (const item of this.selectedItem) {
                    if (item._checked) {
                        this.selectedItem.push(item.id)
                    }
                }
            })
        },
        // 删除
        remove () {
            this.isActionDel = true
        },
        allDelClick () {},
        save () {
            this.$refs.formValidate.validate((valid) => {
                if (valid) {
                    const tempItem = []
                    for (const item of this.selectedItem) {
                        tempItem.push(item.id)
                    }
                    const data = { ...this.taskData, ...{ tempItem } }
                    apiAddTask({ data }).then(({ data }) => {

                    })
                }
            })
        },
        cancel () {},
        detail (row) {
            this.$router.push({path: '/standard-mapper/additional-task/detail'})
        },
        auditStatus (index) {
            // 控制状态切换传值
            this.auditStatusNum = index
        },
        enableStatus (index) {
            // 控制状态切换传值
            this.enableStatusNum = index
        },
        // 选择数据项
        selectItem (selection) {
            this.selectedItem = selection
        }
    },
}
</script>

<style lang="scss" scoped>
h6 {
    padding: 12px 0;
    font-size: $font-size-base;
    font-weight: normal;
    border-top: 2px dashed $border-color;
}
span{
    width: 120px;
    line-height: 30px;
}
p{
    display: inline-block;
    font-size: $font-size-large;
    font-weight: bolder;
    font-family: $base-font-family;
}
.line{
    border:0.5px dashed $border-color;
    margin: 20px 0;
}
// 新增下的列表
.table {
    display: flex;
    p{
        margin: 5px 0;
        font-size: $font-size-base;
    }
    .table-left{
        margin: 0 20px;
    }
}
.search-condition{
    display: flex;
}
.line-margin {
    margin-left: 30px;
}
.icon-action{
    margin: 0 10px;
}
.btn-pagina{
    display: flex;
    padding: 20px 0;

    .btn-operating{
        width: 50%;
    }
    .pagination{
        width: 50%;
        text-align: right;
        margin-right: 2%;
    }
}
</style>
